.lang-add {
  padding: 5px;
  -ms-flex-preferred-size: 1;
      flex-basis: 1;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: end;
      justify-content: flex-end;
}

.lang-add .lang-input {
  padding: 6px 12px;
  height: 27px;
  border: none;
  outline: none;
  font-size: 15px;
  border-bottom: 1px solid #ccc;
}

.lang-add .add-button,
.lang-add .add-button-icon {
  margin: 10px;
  font-size: 12px;
  color: #24292e;
  padding: 3px 10px;
  line-height: 20px;
  border-radius: 3px;
  border: 1px solid #ccc;
  background-color: #eff3f6;
  background-image: -webkit-gradient(linear, left top, left bottom, from(#fafbfc), color-stop(90%, #eff3f6));
  background-image: -webkit-linear-gradient(top, #fafbfc 0%, #eff3f6 90%);
  background-image: -o-linear-gradient(top, #fafbfc 0%, #eff3f6 90%);
  background-image: linear-gradient(-180deg, #fafbfc 0%, #eff3f6 90%);
}

.add-button-icon {
  margin: 5px !important;
}

.lang-add .add-button:active,
.lang-add .add-button-icon:active {
  background-color: #e9ecef;
  background-image: none;
  border-color: rgba(27, 31, 35, 0.35);
  -webkit-box-shadow: inset 0 0.15em 0.3em rgba(27, 31, 35, 0.15);
          box-shadow: inset 0 0.15em 0.3em rgba(27, 31, 35, 0.15);
}

.lang-add .add-button:hover,
.lang-add .add-button-icon:hover {
  background-color: #e6ebf1;
  background-image: -webkit-gradient(linear, left top, left bottom, from(#f0f3f6), color-stop(90%, #e6ebf1));
  background-image: -webkit-linear-gradient(top, #f0f3f6 0%, #e6ebf1 90%);
  background-image: -o-linear-gradient(top, #f0f3f6 0%, #e6ebf1 90%);
  background-image: linear-gradient(-180deg, #f0f3f6 0%, #e6ebf1 90%);
  background-position: -0.5em;
  border-color: rgba(27, 31, 35, 0.35);
}



