.alert-error {
  position: absolute;
  top: 30px;
  right: 15px;
  display: -ms-flexbox;
  display: flex;
  height: 50px;
  color: #856404;
  border-radius: 4px;
  -ms-flex-align: center;
      align-items: center;
  border-color: #ffeeba;
  -ms-flex-pack: center;
      justify-content: center;
  background-color: #fff3cd;
  -webkit-box-shadow: 0px 3px 10px #ccc;
          box-shadow: 0px 3px 10px #ccc;
}

.error-message {
  -ms-flex: 1 1;
      flex: 1 1;
  padding: 0px 10px;
}

.close-error button {
  background: none;
  border: none;
  padding: 15px;
  color: #ccc;
}



